  z-index: 1200;
  transition: opacity 0.4s, visibility 0s linear 0.4s;
  opacity: 0;
  visibility: hidden;
  color: white;
}
@media only screen and (max-width: 640px) {
  .toasty {
    display: none;
  }
}
.toasty.active {
  transition: opacity 0.4s, visibility 0.4s;
  opacity: 1;
  visibility: inherit;
}

.toasty__victor {
  width: 50rem;
  position: relative;
  position: absolute;
  bottom: -7rem;
  right: -7rem;
  transition: transform 0.4s;
  transform: translate(100%, 100%);
}
.toasty.active .toasty__victor {
  transform: translate(0%, 0%);
}
.toasty__victor::before {
  content: "";
  display: block;
  padding-bottom: 100%;
}
.toasty__victor::after {
  content: "";
  position: absolute;
  left: -3rem;
  bottom: 0;
  width: 25rem;
  height: 25rem;
  z-index: 5;
  background-image: url("../img/finger-sprite.png");
  background-size: 200% 100%;
  background-repeat: no-repeat;
  background-position: 0 0;
  animation-name: finger;
  animation-duration: 0.7s;
  animation-timing-function: steps(2);
  animation-iteration-count: infinite;
  animation-fill-mode: forwards;
}

.toasty__victor-message {
  position: absolute;
  right: 100%;
  top: 0;
  margin-top: 5rem;
  font-size: 8rem;
  font-weight: 700;
  line-height: 1.2;
  white-space: nowrap;
}

@keyframes finger {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 200% 0;
  }
}
.toasty__victor-image {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: right bottom;
}